import axios from 'axios';

function errorResponse(error, reject) {
    if (error.response && error.response.data) {
        reject(error.response.data);
    } else {
        reject(error);
    }
}

const app = {
    list() {
        return new Promise(async (resolve, reject) => {
            try {
                const { data: resp } = await axios.get('user/menus');
                resolve(resp.data);
            } catch (error) {
                errorResponse(error, reject);
            }
        });
    },
    grid(option) {
        return new Promise(async (resolve, reject) => {
            const obj = option || {};
            try {
                const keys = ['page', 'limit', 'sortBy', 'sortDir', 'id', 'name', 'icon', 'url', 'seq'];
    
                let str = '';
                for (const key in obj) {
                    if (keys.indexOf(key) >= 0 && obj[key]) {
                        str += str ? '&' : '?';
                        str += key + '=' + encodeURIComponent(obj[key]);
                    }
                }
    
                const url = `/master/menus${str}`;
                const { data } = await axios.get(url);
                const { meta = {}, list } = data;
                obj.total = meta.total;
                resolve(list);
            } catch (error) {
                reject(error);
            }
        });
    },
    async menus() {
        const menus = await app.list();

        menus.forEach(menu => {
            menu.name = menu.name || menu.text;
            if (menu.children) {
                menu.children.forEach(submenu => {
                    submenu.name = submenu.name || submenu.text;
                });
            }
        });

        menus.unshift({ name: 'Home', url: '/home', icon: 'fa fa-home' });

        return new Promise(async (resolve, reject) => {
            try {
                resolve(menus);
            } catch (error) {
                errorResponse(error, reject);
            }
        });
    },
}

export default app;
