import Vue from 'vue';
import Router from 'vue-router';
import routes from './data/routes';
import loader from './utils/loader';
import store from './store';

Vue.use(Router);

loader.loadAuth();
routes.forEach((route) => {
    const row = route;
    const { length } = row.id.split('.');
    const children = routes.filter(
        m => m.id.split(".").length === length + 1 && m.id.indexOf(row.id) === 0
    );

    if (children.length > 0) row.children = children;
    if (row.source) {
        row.component = require(`${row.source}`).default;
    } else {
        row.component = { render: c => c('router-view') };
    }

    const meta = row.meta || {};

    if (row.text) meta.label = row.text;
    if (!row.path) row.path = '';

    row.meta = meta;
    if (row.source) row.name = row.id;

    if (row.secured) {
        row.beforeEnter = (to, form, next) => {
            const { user } = store.state.auth;
            if (user && user.roles) {
                next();
            } else {
                next({ name: 'login' });
            }
        };
    }
});

export default {
    router: () => {
        const resp = new Router({
            base: process.env.BASE_URL,
            linkActiveClass: 'open active',
            scrollBehavior: () => ({ y: 0 }),
            routes: routes.filter(row => (row.id.split('.').length == 1))
        });
        return resp;
    }
};
