<script>
import CartHeader from "../../components/cart-header";
import cartApi from "../../apis/cart";

export default {
  components: { CartHeader },
  data() {
    return {
      items: { meta: {}, data: [] },
      categories: []
    };
  },
  computed: {
    itemMessage() {
      const { items } = this;
      const { meta } = items;

      if (meta) {
        const { limit, page, total } = meta;
        return `${limit * (page - 1)} - ${limit * page} of ${total} items`;
      }
      return "";
    },
    pageMessage() {
      const { items } = this;
      const { meta } = items;

      if (meta) {
        const { limit, page, total } = meta;
        return `${page} of ${Math.ceil(total / limit)} pages`;
      }
      return "";
    }
  },
  created() {
    this.initialize();
  },
  methods: {
    async initialize() {
      const { data: categories } = await cartApi.categories();
      const items = await cartApi.items();

      this.categories = categories;
      this.items = items;
    },
    categClick(id) {
      const { categories } = this;
      categories.forEach(m => {
        if (m.id === id) {
          m.active = true;
        } else {
          m.active = false;
        }
      });
    }
  }
};
</script>
