<template>
  <div class="app flex-row align-items-center">
    <div class="container mt-4">
      <b-row class="justify-content-center">
        <b-col md="8" lg="6">
          <b-card class="text-center">
            <h3 class="font-weight-light">Activate Account</h3>
            <hr>
            <b-form-input size="lg" v-model="model.email" placeholder="Email Address"></b-form-input>
            <b-form-input
              class="mt-2"
              size="lg"
              v-model="model.activation_code"
              placeholder="Activation Code"
              autofocus
            ></b-form-input>
            <b-popover
              :show="pShow"
              target="#activation-code"
              placement="right"
              content="Please, fill out this activation code"
            ></b-popover>
            <hr id="activation-code" class="mt-0" style="border-color:#fff">
            <b-button @click="activate" size="lg" block variant="success">Activate</b-button>
          </b-card>
        </b-col>
      </b-row>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      pShow: false,
      model: { email: "", activation_code: "" }
    };
  },
  computed: {
    activationCode() {
      return this.model.activation_code;
    }
  },
  watch: {
    activationCode: function(val) {
      if (val && val.length) this.pShow = false;
    }
  },
  async created() {},
  methods: {
    async activate() {
      const { $toast } = this;
      $toast.open({
        message: "username_already_activated",
        type: "error",
        position: "bottom"
      });
    }
  }
};
</script>
