
const { localStorage } = global.window || {};

export default {
    get: (key) => {
        const sval = localStorage.getItem(key);
        return sval ? JSON.parse(sval) : null;
    },
    set: (key, value) => {
        localStorage.setItem(key, JSON.stringify(value));
    },
    clear: () => {
        localStorage.clear();
    }
};
