<template>
  <div>
    <sooka-card :schema="schema"></sooka-card>
  </div>
</template>

<script>
export default {
  data() {
    return {
      schema: {
        title: "Sample Title Blank Page",
        subtitle: "Sample Subtitle Blank Page",
        toolbars: [
          { text: "Add Device", click: this.deviceAdd, hide: 1 },
          { text: "Refresh", variant: "success", click: this.deviceRefresh }
        ]
      }
    };
  },
  methods: {
    deviceAdd() {
      this.schema.toolbars[0].hide = 1;
      this.schema.toolbars[1].hide = 0;
    },
    async deviceRefresh() {
      this.schema.toolbars[0].hide = 0;
      this.schema.toolbars[1].hide = 1;
    }
  }
};
</script>
