<template>
  <div class="app flex-row align-items-center">
    <div class="container">
      <b-row class="justify-content-center">
        <b-col md="8">
          <b-card-group>
            <b-card no-body class="p-4">
              <b-card-body>
                <b-form>
                  <div class="text-center mb-4" style="margin-top:-20px">
                    <img src="img/logo-prudential.png" height="50" width="200" alt="Prudential" >
                  </div>
                  <p class="text-muted">Sign In to your account</p>
                  <b-input-group class="mb-3">
                    <b-input-group-prepend>
                      <b-input-group-text>
                        <i class="fa fa-user"></i>
                      </b-input-group-text>
                    </b-input-group-prepend>
                    <b-form-input
                      type="text"
                      class="form-control"
                      ref="username"
                      v-model="model.username"
                      placeholder="Username"
                      autocomplete="username"
                    />
                  </b-input-group>
                  <b-input-group class="mb-4">
                    <b-input-group-prepend>
                      <b-input-group-text>
                        <i class="fa fa-lock"></i>
                      </b-input-group-text>
                    </b-input-group-prepend>
                    <b-form-input
                      type="password"
                      class="form-control"
                      ref="password"
                      v-model="model.password"
                      placeholder="Password"
                      autocomplete="current-password"
                    />
                  </b-input-group>
                  <b-row>
                    <b-col cols="6">
                      <b-button variant="primary" class="px-4" @click="login()">Login</b-button>
                      <!-- <b-button variant="primary" class="px-4" @click="me()">Me</b-button> -->
                    </b-col>
                    <b-col cols="6" class="text-right">
                      <b-button variant="link" class="px-0">Forgot password?</b-button>
                      <!-- <b-link :to="{name:'page.resetpwd'}">Forgot password?</b-link> -->
                    </b-col>
                  </b-row>
                </b-form>
              </b-card-body>
            </b-card>
            <b-card no-body class="text-white bg-secondary py-5 d-md-down-none" style="width:44%">
              <b-card-body class="text-center">
                <div>
                  <br>
                  <br>
                  <br>
                  <b-button
                    block
                    variant="danger"
                    class="active mt-4"
                    @click="register()"
                  >Register Now!</b-button>
                  <!-- <b-button
                    block
                    variant="primary"
                    class="active mt-2"
                    @click="activate()"
                  >Activate Account</b-button> -->
                </div>
              </b-card-body>
            </b-card>
          </b-card-group>
        </b-col>
      </b-row>
    </div>
  </div>
</template>

<script>
import auth from "../../apis/auth";

export default {
  data() {
    return {
      model: { username: "admin", password: "admin-123" }
    };
  },
  created() {
    const { $route } = this;
    const { query } = $route;
    console.log(query);
  },
  beforeCreate() {
    const { $router, $store } = this;
    const { user } = $store.state;

    if (user) {
      const { group } = user;
      if (group === "admin") $router.push({ name: "admin" });
      if (group === "member") $router.push({ name: "app" });
    }
  },
  methods: {
    login: async function() {
      const { $router, $store } = this;
      const { username, password } = this.model;

      if (!username) return;
      if (!password) return;

      try {
        const tk = await auth.login(this.model);
        const me = await auth.current();

        $store.commit("auth/setToken", tk);
        $store.commit("auth/setUser", me);

        console.log({ tk, me });

        if (me.landing_page) {
          $router.push({ name: me.landing_page });
        } else {
          $router.push({ name: "app" });
        }
      } catch (error) {
        console.log(error);
      }
    },
    me: async function() {
      const me = await this.$http.get("auth/me");
      console.log(me);
    },
    register() {
      this.$router.push({ name: "register" });
    },
    activate() {
      this.$router.push({ name: "activation" });
    }
  }
};
</script>
