<template>
  <div>
    <Header>
      <SidebarToggler class="d-lg-none" display="md" mobile />
      <b-link class="navbar-brand" :to="{name: 'app'}">
        <img class="navbar-brand-full app-logo" src="img/logo.png" />
      </b-link>
      <b-navbar-nav class="d-md-down-none">
        <b-dropdown variant="link" text="Shortcut Menus" class="m-2">
          <div class="shortcut-link p-3 mt-2 mb-2">
            <b-shortcut :menus="menus"></b-shortcut>
          </div>
        </b-dropdown>
      </b-navbar-nav>
      <b-navbar-nav class="d-md-down-none ml-auto">
        <b-nav-item class="d-md-down-none">
          <i class="fa fa-envelope"></i>
          <b-badge pill variant="danger">2</b-badge>
        </b-nav-item>
        <b-account />
      </b-navbar-nav>
    </Header>
  </div>
</template>

<style lang="scss" scoped>
.app-header {
  .app-logo {
    height: 52px;
  }
}

.breadcrumb {
  margin-bottom: 15px;
}

.container-fluid {
  padding: 0 15px;
}
</style>

<script>
import oMenu from "../apis/menu";
import oAuth from "../apis/auth";

import {
  Header,
  HeaderDropdown,
  SidebarToggler,
  Sidebar,
  SidebarFooter,
  SidebarForm,
  SidebarHeader,
  SidebarMinimizer,
  SidebarNav,
  AsideToggler,
  Footer,
  Breadcrumb
} from "@coreui/vue";
import bShortcut from "./shortcut-menus";
import bAccount from "./acc-admin";

export default {
  components: {
    Header,
    HeaderDropdown,
    SidebarToggler,
    Sidebar,
    SidebarFooter,
    SidebarForm,
    SidebarHeader,
    SidebarMinimizer,
    SidebarNav,
    AsideToggler,
    Footer,
    Breadcrumb,
    bShortcut,
    bAccount
  },
  data() {
    return {
      page: { showLoginForm: false },
      model: {},
      menus: []
    };
  },
  computed: {
    name() {
      return this.$route.name;
    },
    breadcrumb() {
      const list = this.$route.matched.filter(r => r.name || r.meta.label);
      list.shift();
      return list;
    },
    user() {
      return this.$store.state.auth.user;
    }
  },
  async mounted() {
    const $this = this;
    try {
      $this.menus = await oMenu.menus();
    } catch (error) {
      if (error.message === "jwt expired") {
        console.log("----", error);
        $this.refresh();
      } else {
        oAuth.signout();
      }
    }
  },
  methods: {
    signout() {
      oAuth.signout();
    },
    loginValidation() {
      const { model, $toast } = this;
      if (!model.username) {
        $toast.open({
          message: "please input username",
          type: "error",
          position: "bottom"
        });
        return false;
      }
      if (!model.password) {
        $toast.open({
          message: "please input password",
          type: "error",
          position: "bottom"
        });
        return false;
      }

      return true;
    },
    async refresh() {
      const { $store, page } = this;
      const token = $store.state.auth.token;

      try {
        const tk = await oAuth.refresh(token);
        const me = await oAuth.current();
        $store.commit("auth/setToken", tk);
        $store.commit("auth/setUser", me);
        page.validated = true;
      } catch (error) {
        page.showLoginForm = true;
      }
    },
    async login() {
      const { page, model, $store } = this;

      if (this.loginValidation()) {
        try {
          const tk = await oAuth.login(model);
          const me = await oAuth.current();

          $store.commit("auth/setToken", tk);
          $store.commit("auth/setUser", me);

          page.showLoginForm = false;
          page.validated = true;
        } catch (error) {
          console.log(error);
          page.showLoginForm = true;
        }
      }
    },
    onLoginClose() {
      const { page } = this;
      if (page.validated) return;

      setTimeout(function() {
        page.showLoginForm = true;
      }, 1000);
    }
  }
};
</script>
