const app = {
    categories: () => {
        return new Promise(async (resolve, reject) => {
            try {
                const data = import('../mocks/item_categories.json');
                resolve(data);
            } catch (error) {
                reject(error);
            }
        })
    },
    items: () => {
        return new Promise(async (resolve, reject) => {
            // http://lorempixel.com/400/400/technics/2/
            try {
                const resp = {
                    message: 'list_items',
                    meta: {
                        page: 2,
                        limit: 50,
                        total: 719
                    },
                    data: []
                };
                const { meta, data } = resp;
                const { limit, page } = meta;

                for (let i = limit * (page - 1); i < limit * page; i += 1) {
                    data.push({
                        id: i,
                        // src: `http://lorempixel.com/400/400/technics/${i + 1}`,
                        src: `https://picsum.photos/id/${i + 1}/300/300`
                    })
                }
                resolve(resp);
            } catch (error) {
                reject(error);
            }
        })
    }
}

export default app;
