export default {
    list() {
        return new Promise(async (resolve, reject) => {
            try {
                const resp = { data: dummy.items };
                resolve(resp);
            } catch (error) {
                reject(error);
            }
        });
    },
    detail(id) {
        return new Promise(async (resolve, reject) => {
            try {
                const resp = dummy.items.find(m => m.id === id);
                resolve(resp);
            } catch (error) {
                reject(error);
            }
        });
    }
};
