<template>
  <div class="app">
    <app-header></app-header>
    <div class="app-body">
      <Sidebar mobile display="md" fixed>
        <SidebarNav :navItems="menus"></SidebarNav>
        <SidebarFooter/>
      </Sidebar>
      <main class="main">
        <breadcrumb :list="breadcrumb"/>
        <div class="container-fluid">
          <router-view></router-view>
        </div>
      </main>
    </div>
    <b-modal v-model="page.showLoginForm" title="Input Username & password" @hide="onLoginClose">
      <b-form-row class>
        <label class="col-lg-3 col-form-label">Username</label>
        <b-col class="col-lg-9">
          <b-form-input v-model="model.username"></b-form-input>
        </b-col>
      </b-form-row>
      <b-form-row class="mt-2">
        <label class="col-lg-3 col-form-label">Password</label>
        <b-col class="col-lg-9">
          <b-input type="password" v-model="model.password"></b-input>
        </b-col>
      </b-form-row>

      <div slot="modal-footer">
        <b-button variant="primary" @click="login">Login</b-button>
      </div>
    </b-modal>
  </div>
</template>

<style lang="scss" scoped>
.breadcrumb {
  margin-bottom: 15px;
}

.container-fluid {
  padding: 0 15px;
}
</style>

<script>
import oMenu from "../apis/menu";
import oAuth from "../apis/auth";

import {
  SidebarToggler,
  Sidebar,
  SidebarFooter,
  SidebarForm,
  SidebarHeader,
  SidebarMinimizer,
  SidebarNav,
  AsideToggler,
  Footer,
  Breadcrumb
} from "@coreui/vue";
import AppHeader from "../components/app-header";

export default {
  components: {
    AppHeader,
    Sidebar,
    SidebarFooter,
    SidebarForm,
    SidebarHeader,
    SidebarMinimizer,
    SidebarNav,
    AsideToggler,
    Footer,
    Breadcrumb,
  },
  data() {
    return {
      page: { showLoginForm: false },
      model: {},
      menus: []
    };
  },
  computed: {
    name() {
      return this.$route.name;
    },
    breadcrumb() {
      const list = this.$route.matched.filter(r => r.name || r.meta.label);
      list.shift();
      return list;
    },
    user() {
      return this.$store.state.auth.user;
    }
  },
  async mounted() {
    const $this = this;
    try {
      $this.menus = await oMenu.menus();
    } catch (error) {
      if (error.message === "jwt expired") {
        console.log("----", error);
        $this.refresh();
      } else {
        oAuth.signout();
      }
    }
  },
  methods: {
    loginValidation() {
      const { model, $toast } = this;
      if (!model.username) {
        $toast.open({
          message: "please input username",
          type: "error",
          position: "bottom"
        });
        return false;
      }
      if (!model.password) {
        $toast.open({
          message: "please input password",
          type: "error",
          position: "bottom"
        });
        return false;
      }

      return true;
    },
    async refresh() {
      const { $store, page } = this;
      const token = $store.state.auth.token;

      try {
        const tk = await oAuth.refresh(token);
        const me = await oAuth.current();
        $store.commit("auth/setToken", tk);
        $store.commit("auth/setUser", me);
        page.validated = true;
      } catch (error) {
        page.showLoginForm = true;
      }
    },
    async login() {
      const { page, model, $store } = this;

      if (this.loginValidation()) {
        try {
          const tk = await oAuth.login(model);
          const me = await oAuth.current();

          $store.commit("auth/setToken", tk);
          $store.commit("auth/setUser", me);

          page.showLoginForm = false;
          page.validated = true;
        } catch (error) {
          console.log(error);
          page.showLoginForm = true;
        }
      }
    },
    onLoginClose() {
      const { page } = this;
      if (page.validated) return;

      setTimeout(function() {
        page.showLoginForm = true;
      }, 1000);
    }
  }
};
</script>
