export default {
    namespaced: true,
    state: {
        breadcrumb: [
            { to: "/", text: "Home" },
        ],
        title: '-'
    },
    getters: {},
    actions: {},
    mutations: {
        breadcrumb(state, breadcrumb) {
            state.breadcrumb = breadcrumb;
        },
        title(state, title) {
            state.title = title;
        },
    }
};
