<template>
  <div class="animated fadeIn">
    <b-row class="justify-content-center">
      <b-col>
        <b-card header="List Available Content">
          <b-row>
            <b-col class="box col-md-6 col-lg-4" v-for="(menu, idx) in menus" :key="idx">
              <div v-if="menu.children">
                <h5>
                  <i :class="menu.icon"></i>
                  {{menu.text}}
                </h5>
                <ul>
                  <li class="sub-link" v-for="(child, idx) in menu.children" :key="idx">
                    <router-link :to="{path:child.url}" v-if="!child.children">
                      <i :class="child.icon"></i>
                      {{child.text}}
                      <span
                        v-if="child.badge"
                        class="badge badge-pill badge-danger"
                      >{{child.badge.text}}</span>
                    </router-link>

                    <div v-if="child.children">
                      <label>
                        <i :class="child.icon"></i>
                        {{child.text}}
                      </label>
                      <ul>
                        <li v-for="(child1, idx) in child.children" :key="idx">
                          <router-link :to="{path:child1.url}">
                            <i :class="child1.icon"></i>
                            {{child1.text}}
                            <span
                              v-if="child1.badge"
                              class="badge badge-pill badge-danger"
                            >{{child1.badge.text}}</span>
                          </router-link>
                        </li>
                      </ul>
                    </div>
                  </li>
                </ul>
              </div>
              <div v-else>
                <router-link :to="{path:menu.url}">
                  <i :class="menu.icon"></i>
                  {{menu.text}}
                  <span
                    v-if="menu.badge"
                    class="badge badge-pill badge-danger"
                  >{{menu.badge.text}}</span>
                </router-link>
              </div>
            </b-col>
          </b-row>
        </b-card>
      </b-col>
    </b-row>
  </div>
</template>

<style lang="scss" scoped>
h5 {
  i {
    margin-right: 5px;
  }
}

ul {
  padding-left: 0;

  .sub-link {
    display: block;
    margin-left: 10px;

    i {
      width: 20px;
    }

    li {
      display: block;
      padding-left: 20px;

      i {
        width: 15px;
      }
    }
  }
}
</style>


<script>
import oMenu from "../apis/menu";

export default {
  computed: {
    menus() {
      return this.$store.state.menu.list;
    }
  },
  created() {
    this.initial();
  },
  methods: {
    async initial() {
      const { $store } = this;
      const menus = await oMenu.list();
      $store.commit("menu/list", menus);
    }
  }
};
</script>
