<template>
  <AppHeaderDropdown right no-caret class="mr-4">
    <template slot="header">
      <span>
        <img
          src="img/avatar.jpg"
          class="img-avatar"
          width="30"
          height="30"
          :alt="user.username"
        >
      </span>
    </template>
    <template slot="dropdown">
      <b-dropdown-header tag="div" class="thin-border">
        <strong>Hi, {{user.firstname}} {{user.lastname}}</strong>
      </b-dropdown-header>
      <b-dropdown-item
        @click="linkClick(menu)"
        class="thin-border"
        v-for="(menu, idx) in menus"
        :key="idx"
      >{{menu.title}}</b-dropdown-item>
      <b-dropdown-item @click="signout">Sign Out</b-dropdown-item>
    </template>
  </AppHeaderDropdown>
</template>

<style lang="scss" scoped>
.thin-border {
  border-bottom: 1px solid rgba(0, 0, 0, 0.04);
}
.dropdown-item {
  padding: 8px 20px;
  color: #73818f;
}
</style>

<script>
import { HeaderDropdown as AppHeaderDropdown } from "@coreui/vue";
import oAuth from "../apis/auth.js";

export default {
  components: {
    AppHeaderDropdown
  },
  data: () => {
    return {
      menus: [],
      user: {}
    };
  },
  created: async function() {
    this.menus = [{ title: "Profile", name: "admin.profile" }];
    this.user = await oAuth.current();
  },
  methods: {
    linkClick(menu) {
      console.log(menu);
      // if (menu.name) this.$router.push({ name: menu.name });
    },
    signout() {
      localStorage.clear();
      location.href = "/";
    }
  }
};
</script>
