
import storage from './storage';
import auth from '../apis/auth';

const { location } = global.window || {};

export default {
    signout() {
        storage.clear();
        location.href = "/";
    },
    current() {
        return auth.current();
    },
    login(user) {
        return auth.login(user);
    },
    refresh(token) {
        return auth.refresh(token);
    }
};
