<template>
  <div>
    <div class="item-detail">
      <b-row>
        <b-col lg="5">
          <div class="item-image">
            <div class="image-media">
              <b-img :src="model.src"></b-img>
            </div>
          </div>
          <div class="thumb-image">
            <b-row no-gutters>
              <b-col>
                <div class="img">
                  <b-img :src="model.src"></b-img>
                </div>
              </b-col>
              <b-col>
                <div class="img">
                  <b-img :src="model.src"></b-img>
                </div>
              </b-col>
              <b-col>
                <div class="img">
                  <b-img :src="model.src"></b-img>
                </div>
              </b-col>
            </b-row>
          </div>
        </b-col>
        <b-col lg="7">
          <div class="info">
            <h1 class="title">{{model.sku_name}}</h1>
            <div class="price">Rp {{model.price}}</div>
            <div class="quantity">
              <strong>Jumlah</strong>
              <b-input-group>
                <b-input-group-prepend>
                  <b-button variant="secondary" class="pl-4 pr-4">-</b-button>
                </b-input-group-prepend>
                <b-form-input class="text-right" v-model="model.qty"></b-form-input>
                <b-input-group-append>
                  <b-button variant="secondary" class="pl-4 pr-4">+</b-button>
                </b-input-group-append>
              </b-input-group>
            </div>
            <div class="description">
              <strong>Deskripsi</strong>
              <p>{{model.info}}</p>
            </div>
            <div class="action">
              <b-button block variant="success" @click="addToCart">Beli Sekarang</b-button>
            </div>
          </div>
        </b-col>
      </b-row>
    </div>
  </div>
</template>

<style lang="scss">
.item-detail {
  .item-image {
    border: 1px solid #ddd;
    padding-top: 0;
    border-radius: 5px;
    padding: 5px;

    .image-media {
      border: 1px solid #ddd;

      img {
        width: 100%;
      }
    }
  }

  .thumb-image {
    margin-left: -5px;
    margin-right: -5px;
    margin-top: 10px;

    .img {
      border: 1px solid #ddd;
      border-radius: 5px;
      margin: 5px;
      padding: 5px;
    }

    img {
      width: 100%;
    }
  }

  .info {
    .price {
      color: #cc0000;
      font-size: 20px;
      margin-top: 40px;
    }

    .quantity {
      margin-top: 40px;
      max-width: 250px;
    }

    .description {
      margin-top: 40px;
    }

    .action {
      margin-top: 40px;
    }
