import axios from 'axios';
import storage from '../utils/storage';

function errorResponse(error, reject) {
    if (error.response && error.response.data) {
        reject(error.response.data);
    } else {
        reject(error);
    }
}

export default {
    login(user) {
        return new Promise(async (resolve, reject) => {
            try {
                const { data: resp } = await axios.post('auth/login', user);
                axios.defaults.headers.Authorization = resp.data.token;
                resolve(resp.data);
            } catch (error) {
                errorResponse(error, reject);
            }
        });
    },
    refresh(token) {
        return new Promise(async (resolve, reject) => {
            try {
                const { data } = await axios.post('auth/refresh', token);
                axios.defaults.headers.Authorization = data.token;
                resolve(data);
            } catch (error) {
                errorResponse(error, reject);
            }
        });
    },
    current() {
        return new Promise(async (resolve) => {
            try {
                const { data: resp } = await axios.get('user/me');
                resolve(resp.data);
            } catch (error) {
                storage.clear();
                location.href = '/';
            }
        });
    },
};
