<template>
  <div>
    <sooka-card :schema="schema">
      <div>
        <b-table
          class="mb-0"
          outlined
          stacked="lg"
          :ref="tblData.refs||'table'"
          :striped="tblData.striped"
          :items="provider"
          :fields="tblData.fields"
          :current-page="tblData.page"
          :per-page="tblData.limit"
          :filterName="tblData.filterName"
          :sort-by.sync="tblData.sortBy"
          :sort-desc.sync="tblData.sortDesc"
        ></b-table>
        <b-row class="mt-2">
          <b-col cols="6">
            <div>
              Showing
              {{(tblData.page - 1) * tblData.limit + 1}}
              to
              {{tblData.page * tblData.limit}}
              of
              {{tblData.total}}
              entries
            </div>
          </b-col>
          <b-col cols="6">
            <b-pagination
              align="right"
              class="mb-0"
              :total-rows="tblData.total"
              :per-page="tblData.limit"
              v-model="tblData.page"
            ></b-pagination>
          </b-col>
        </b-row>
      </div>
    </sooka-card>
  </div>
</template>

<script>
import oMenu from "../../apis/menu";

export default {
  data() {
    return {
      schema: {
        title: "Sample List of Data"
      },
      filter: {},
      tblData: {
        refs: "table1",
        fields: [
          { key: "id", label: "ID" },
          { key: "name", label: "Name" },
          { key: "icon", label: "Icon" },
          { key: "url", label: "URL" },
          { key: "seq", label: "Sequence" }
        ],
        page: 1,
        limit: 10,
        total: 0,
        striped: true
      }
    };
  },
  methods: {
    async provider() {
      const { tblData, filter } = this;
      tblData.sortDir = tblData.sortDesc ? "desc" : "asc";

      const keys = ["id", "name", "icon", "url", "seq"];

      keys.forEach(key => {
        tblData[key] = filter[key] ? `%${filter[key]}%` : "";
      });

      console.log("-->", tblData);

      return await oMenu.grid(tblData);
    }
  }
};
</script>
