<template>
  <div>
    <b-card-group>
      <b-card>
        <h6 class="card-header">Katalog Souvenir</h6>
        <b-row>
          <b-col
            class="item-container"
            md="6"
            lg="4"
            xl="3"
            v-for="(item, idx) in catalog.items"
            :key="idx"
            @click="itemClick(item)"
          >
            <div class="item">
              <img :src="item.src" class="img-fluid" alt />
            </div>
            <div class="item-info">
              <div>
                <div class="title">{{item.sku_name}}</div>
                <div class="price">Rp. {{item.price}}</div>
              </div>
            </div>
          </b-col>
        </b-row>
      </b-card>
    </b-card-group>
  </div>
</template>

