<script>
import oCatalog from "../../apis/catalog";

export default {
  data() {
    return {
      catalog: {
        meta: {},
        items: []
      }
    };
  },
  created() {
    this.initial();
  },
  methods: {
    async initial() {
      const { data, meta } = await oCatalog.list();
      this.catalog.meta = meta;
      this.catalog.items = data;
    },
    itemClick(item) {
      const { $router } = this;
      $router.push({ name: "public.detail", params: { id: item.id } });
      console.log(item);
    }
  }
};
</script>
