import storage from '../../utils/storage';

const APP_AUTH_ME = process.env.VUE_APP_AUTH_ME;
const APP_AUTH_TK = process.env.VUE_APP_AUTH_TK;

export default {
    namespaced: true,
    state: { user: { username: 'guest' } },
    getters: {},
    actions: {},
    mutations: {
        setUser(state, user) {
            state.user = user;
            storage.set(APP_AUTH_ME, user);
        },
        setToken(state, token) {
            state.token = token;
            storage.set(APP_AUTH_TK, token);
        },
    }
};
