import axios from 'axios';

import storage from './storage';
import store from '../store';

export default {
    loadAuth: () => {
        const APP_AUTH_ME = process.env.VUE_APP_AUTH_ME;
        const APP_AUTH_TK = process.env.VUE_APP_AUTH_TK;

        const me = storage.get(APP_AUTH_ME);
        const tk = storage.get(APP_AUTH_TK);

        if (me && tk) {
            store.state.auth.user = me;
            store.state.auth.token = tk;
            axios.defaults.headers.Authorization = tk.token;
        }
    }
};
