<template>
  <div>
    <sooka-card :schema="schema">
      <div class="filter">
        <b-row>
          <b-col xl="8" lg="10">
            <b-form-group>
              <b-form-row>
                <label class="col-2">ID</label>
                <div class="col-7">
                  <input type="text" @change="refreshData" v-model="filter.id" class="form-control">
                </div>
              </b-form-row>
            </b-form-group>
            <b-form-group>
              <b-form-row>
                <label class="col-2">Name</label>
                <div class="col-7">
                  <input
                    type="text"
                    @change="refreshData"
                    v-model="filter.name"
                    class="form-control"
                  >
                </div>
              </b-form-row>
            </b-form-group>
          </b-col>
        </b-row>
      </div>
      <div>
        <b-table
          class="mb-0"
          outlined
          stacked="lg"
          :ref="tblData.refs||'table'"
          :striped="tblData.striped"
          :items="provider"
          :fields="tblData.fields"
          :current-page="tblData.page"
          :per-page="tblData.limit"
          :filterName="tblData.filterName"
          :sort-by.sync="tblData.sortBy"
          :sort-desc.sync="tblData.sortDesc"
        >
          <template slot="action" slot-scope="data">
            <a href="javascript:void(0)" @click="editData(data.item)">
              <i class="fa fa-pencil"></i>
            </a>
            |
            <a
              href="javascript:void(0)"
              class="text-danger"
              @click="deleteConfirm(data.item)"
            >
              <i class="fa fa-trash"></i>
            </a>
          </template>
        </b-table>
        <b-row class="mt-2">
          <b-col cols="6">
            <div>
              Showing
              {{(tblData.page - 1) * tblData.limit + 1}}
              to
              {{tblData.page * tblData.limit}}
              of
              {{tblData.total}}
              entries
            </div>
          </b-col>
          <b-col cols="6">
            <b-pagination
              align="right"
              class="mb-0"
              :total-rows="tblData.total"
              :per-page="tblData.limit"
              v-model="tblData.page"
            ></b-pagination>
          </b-col>
        </b-row>
      </div>
    </sooka-card>
    <b-modal v-model="page.deleteConfirm" title="Delete Record?" :centered="true" @ok="removeData">
      <p class="mb-1">"{{page.deleteItem.id}}" will be permanently deleted</p>
      <p class="font-weight-bold m-0 text-danger">Warning: You can't undo this action.</p>
    </b-modal>
    <b-modal v-model="page.displayDetail" size="lg" :title="page.detailTitle" :centered="true">
      <b-form-group>
        <b-form-row>
          <label class="col-3">ID</label>
          <div class="col-9">
            <input
              type="text"
              :readonly="page.state=='edit'"
              v-model="model.id"
              class="form-control"
            >
          </div>
        </b-form-row>
      </b-form-group>
      <b-form-group>
        <b-form-row>
          <label class="col-3">Name</label>
          <div class="col-9">
            <input type="text" v-model="model.name" class="form-control">
          </div>
        </b-form-row>
      </b-form-group>
      <b-form-group>
        <b-form-row>
          <label class="col-3">Icon</label>
          <div class="col-9">
            <input type="text" v-model="model.icon" class="form-control">
          </div>
        </b-form-row>
      </b-form-group>
      <b-form-group>
        <b-form-row>
          <label class="col-3">URL</label>
          <div class="col-9">
            <input type="text" v-model="model.url" class="form-control">
          </div>
        </b-form-row>
      </b-form-group>
      <b-form-group>
        <b-form-row>
          <label class="col-3">Sequence</label>
          <div class="col-9">
            <input type="text" v-model="model.seq" class="form-control">
          </div>
        </b-form-row>
      </b-form-group>
    </b-modal>
  </div>
</template>

<script>
import oMenu from "../../apis/menu";

export default {
  data() {
    return {
      schema: {
        title: "List of Menu",
        toolbars: [
          { text: "Add Menu", icon: "fa fa-file", click: this.addData },
          {
            text: "Refresh",
            icon: "fa fa-refresh",
            variant: "success",
            click: this.refreshData
          }
        ]
      },
      filter: {},
      tblData: {
        refs: "table1",
        fields: [
          { key: "id", label: "ID" },
          { key: "name", label: "Name" },
          { key: "icon", label: "Icon" },
          { key: "url", label: "URL" },
          { key: "seq", label: "Sequence" },
          {
            key: "action",
            thStyle: "width:150px",
            thClass: "text-center",
            tdClass: "text-center"
          }
        ],
        page: 1,
        limit: 10,
        total: 0,
        striped: true
      },
      page: {
        deleteItem: {},
        deleteConfirm: false,
        displayDetail: false,
        detailTitle: "Add New Data"
      },
      model: {}
    };
  },
  methods: {
    async provider() {
      const { tblData, filter } = this;
      tblData.sortDir = tblData.sortDesc ? "desc" : "asc";

      const keys = ["id", "name"];

      keys.forEach(key => {
        tblData[key] = filter[key] ? `%${filter[key]}%` : "";
      });

      return await oMenu.grid(tblData);
    },
    addData() {
      this.model = {};
      this.page.displayDetail = true;
      this.page.detailTitle = "Add New Data";
      this.page.state = "new";
    },
    editData(item) {
      this.model = item;
      this.page.displayDetail = true;
      this.page.detailTitle = "Edit Data";
      this.page.state = "edit";
    },
    removeData(item) {
      const { $toast, page } = this;
      $toast.open({
        message: `${page.deleteItem.id} (${page.deleteItem.name}) deleted`,
        type: "error",
        position: "bottom"
      });
    },
    refreshData() {
      const { filter } = this;
      console.log(filter);
      this.$refs.table1.refresh();
    },
    deleteConfirm(item) {
      this.page.deleteItem = item;
      this.page.deleteConfirm = true;
    }
  }
};
</script>
